@import '../../variables.scss';
@import '../../mixin.scss';

.tabStyle {
	:global {
		.nav-pills {
			width: 100%;
			background-color: #EFEFF2;
			padding: 4px;
			border-radius: 20px;

			.nav-item {
				flex-grow: 1;
				text-align: center;
				text-transform: capitalize;

				.nav-link {
					color: $Grey;
					font-size: 14px;
					font-weight: 400;

					&:hover {
						cursor: pointer;
						color: $Black;
					}

					&.active {
						background-color: $PureWhite;
						color: $DarkBlack;
						font-weight: 500;
						border-radius: 16px;
					}
				}
			}
		}
	}
}

.lockLayout {
	.title {
		background-color: black;
		padding: 2em;

		h1 {
			@include Barlow;
			font-size: 36px;
			color: $PureWhite;
			text-align: center;
			text-transform: capitalize;
		}

		.description {
			width: 100%;
			@include Barlow;
			font-size: 16px;
			color: $PureWhite;
			font-weight: 400;
			text-align: center;
		}
	}

	.intro {
		background-color: black;
		padding: 0 0 1.5em 0;
		display: flex;
		justify-content: center;
		align-items: center;
		color: white;

		.introContent {
			display: flex;
			justify-content: center;
			align-items: flex-start;
			flex-direction: row;
			width: fit-content;

			.leftPane {
				margin-right: 0.5em;
				border: 1px solid #FFFFFF;
				border-radius: 12px;
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				align-items: flex-start;
				padding: 1em;
				width: 590px;

				.scoreBG {
					background-image: url("../../images/v2/score_bg.png");
					background-size: 558px 112px;
					border-radius: 16px;
					padding: 1em;
					display: flex;
					flex-direction: row;
					justify-content: space-between;
					align-items: center;
					width: 558px;
					height: 112px;
					padding-left: 38px;
					padding-right: 32px;

					.titleLabel {
						display: flex;
						flex-direction: row;
						justify-content: flex-start;
						align-items: center;
						line-height: 1;

						img {
							margin-right: 0.5em
						}

						.titleLabelKey {
							@include Barlow;
							font-weight: 400;
							font-size: 14px;
							color: #FFFFFF;
						}

						.titleLabelValue {
							@include Barlow;
							font-weight: 600;
							font-size: 36px;
							color: #FFFFFF;
						}
					}
				}

				.descriptionList {
					@include Barlow;
					font-weight: 400;
					font-size: 14px;
					line-height: 24px;
					color: #EFEFF2;
				}
			}

			.rightPane {
				border: 1px solid #FFFFFF;
				border-radius: 12px;
				margin-left: 0.5em;
				padding: 1em;
				width: 590px;
				height: 346px;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;

				.chartInfo {
					@include Barlow;
					font-weight: 500;
					font-size: 14px;
					color: #FF8E3D;
					display: flex;
					flex-direction: row;
					align-items: center;

					img {
						margin-right: 0.5em;
					}
				}

				.canvasContainer {
					width: 100%;
					text-align: center;
				}
			}
		}
	}

	.content {
		background: #F6F6F6;
		width: 1200px;
		margin-left: calc((100vw - 1200px) / 2);
		padding: 1.5em 0;

		.countDownBar {
			background: #FFFFFF;
			border-radius: 22px;
			padding: 12px 22px;
			width: fit-content;
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: center;
			@include Barlow;
			font-weight: 400;
			font-size: 16px;
			color: #040A2D;
			text-transform: capitalize;

			.countDownValues {
				vertical-align: baseline;

				span {
					text-transform: uppercase
				}

				.countDownValue {
					@include Barlow;
					font-weight: 500;
					font-size: 24px;
					color: #00D092;
				}
			}
		}

		.tableContainer {
			padding: 1em;
			background: #FFFFFF;
			border-radius: 16px;
			width: 100%;
			margin: 1em 0;

			table {
				width: 100%;
			}

			table thead tr th {
				@include Barlow;
				text-transform: capitalize;
				padding: 0.5em 0.5em 1em 0.5em;
				font-weight: 500;
				font-size: 16px;
				color: #040A2D;
			}

			table thead tr th .number {
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
			}

			table tbody tr td:first-child {
				text-transform: capitalize;
				@include Barlow;
			}

			table tbody tr td {
				@include Barlow;
				text-transform: capitalize;
				padding: 0.5em 0.5em;

				.label {
					display: none;
				}
			}

			table thead tr .number {
				text-align: right;
			}

			table thead tr .number .tips {
				font-size: 16px;
				color: lightgray;
			}

			table tbody tr .number {
				text-align: right;
			}
		}

		.rowContainer {
			width: 100%;
			display: grid;
			grid-template-columns: 50% 50%;
			margin: 2em 0;
			font-size: 16px;
			height: fit-content;
			column-gap: 1em;
			justify-items: start;
			// align-items: start;
		}
	}
}

.panel {
	border-radius: 16px;
	background-color: white;
	padding: 2em;
	width: 590px;
	margin: 0 0.5em;

	.tabPane {
		padding: 1em 0;

		.keyAndValue {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			text-transform: capitalize;

			.key {
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}

		.assetsLabel {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			text-transform: capitalize;
			background: #EFEFF2;
			border: 1px solid #DBDBE6;
			border-radius: 12px;
			padding: 1em;
		}

		button {
			@include Barlow;
			width: 100%;
			background: #00D092;
			border-radius: 24px;
			padding: 13px 0;
			color: white;
			text-transform: capitalize;
			font-weight: 500;
			font-size: 18px;
			border: none;
			cursor: pointer;

			&:disabled {
				filter: grayscale(50%);
				pointer-events: none;
				cursor: not-allowed;
			}
		}

		.buttonBordred {
			border: 1px solid #00D092;
			border-radius: 24px;
			padding: 13px 2em;
			width: fit-content;
			background-color: white;
			color: black;
			font-weight: 500;
			font-size: 18px;
			color: #00D092;

			&:disabled {
				filter: grayscale(100%);
			}
		}
	}
}

.scoreLabelLayout {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;

	img {
		width: 48px;
	}

	.info {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		margin-left: 1em;
		min-height: 54px;

		.infoTitle {
			@include Barlow;
			font-weight: 400;
			font-size: 14px;
			color: #494E67;
			margin-bottom: 0.5em
		}

		.infoValue {
			@include Barlow;
			font-weight: 700;
			font-size: 28px;
			color: #040A2D;
			margin-bottom: 0.2em;
			text-transform: capitalize
		}
	}
}

.scoreDescription {
	@include Barlow;
	font-weight: 400;
	font-size: 14px;
}

.expandableLabelLayout {
	width: 100%;
	border-radius: 16px;
	background-color: white;
	margin: 0;
	padding: 1em;
	margin-bottom: 1em;

	.label {
		@include Barlow;
		font-size: 18px;
		color: #040A2D;
		width: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		cursor: pointer;

		.text {
			width: 90%;
		}

		.icon {
			color: #00D092;
			font-size: 18px;
		}
	}

	.expandableContent {
		margin: 1em 0 0 0;
	}
}

.poolListItem {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	width: 100%;

	.poolListItemLeftLabel {
		display: flex;
		flex-direction: row;

		.poolListItemTitle {
			@include Barlow;
			font-weight: 500;
			font-size: 16px;
			color: #040A2D;
		}
	}

	.poolListItemRightLabel {
		width: fit-content;
	}
}

.lockModal .modal-dialog .modal-content {
	height: fit-content;
}

.modalTitle {
	@include Barlow;
	font-weight: 500;
	font-size: 16px;
	color: #040A2D;
	text-align: left;
	width: 100%;
	display: flex;
	align-items: center;
	text-transform: capitalize;
}

.modalBodyCenter {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-top: 40px;
}

.modalBody {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;

	.key {
		@include Barlow;
		font-weight: 400;
		font-size: 14px;
		color: #040A2D;
		text-transform: capitalize;
		margin: 0.5em 0
	}

	.value {
		@include Barlow;
		font-weight: 700;
		font-size: 20px;
		color: #00D092;
	}

	.labelFullWidth {
		width: 100%;
		font-size: 14px;
		text-align: center;
	}
}

.modalButtons {
	margin-top: 2em;
	width: 100%;

	button {
		@include Barlow;
		width: 100%;
		background: #00D092;
		border-radius: 24px;
		padding: 10px 0;
		color: white;
		text-transform: capitalize;
		font-weight: 500;
		font-size: 18px;
		border: none;
		cursor: pointer;

		&:disabled {
			filter: grayscale(50%);
			pointer-events: none;
			cursor: not-allowed;
		}
	}
}

.buttonFitContent {
	@include Barlow;
	width: fit-content;
	height: fit-content;
	background: #00D092;
	border-radius: 24px;
	padding: 13px 2em;
	color: white !important;
	text-transform: capitalize;
	font-weight: 500;
	font-size: 18px;
	border: none;
	cursor: pointer;
	text-decoration: none;

	&:disabled {
		filter: grayscale(50%);
		pointer-events: none;
		cursor: not-allowed;
	}
}

.smallButtonBordred {
	background: #FFFFFF;
	border: 1px solid #00D092;
	border-radius: 18px;
	padding: 8px 30px;
	width: fit-content;
	color: black;
	font-weight: 500;
	font-size: 14px;
	color: #00D092;

	&:disabled {
		opacity: 0.5;
	}
}